export default {
  data() {
    return {
      delPhotoData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        }
      ],
      total: "",
      pageNum: 1
    };
  },
  created() {
    this.getPhotos();
  },
  methods: {
    async getPhotos(page = 1) {
      let res = await this.$axios.get(
        `http://127.0.0.1:8080/blogs/photo/qyPhoto.php?page=${page}`
      );
      console.log(res);
      if (res.data[0].status == 200) {
        res.data[1][0].path
          ? (res.data[1][0].path = res.data[1][0].path.substr(29, 19))
          : "";
        res.data[1][1]
          ? (res.data[1][1].path = res.data[1][1].path.substr(29, 19))
          : "";
        this.delPhotoData = res.data[1];
        // console.log(res1);
        this.total = res.data[2][0].total;
      }
    },
    // 
    setPage(num) {
      this.getPhotos(num);
      this.pageNum = num;
    },
    //
    async delPhoto(row) {
      try {
        await this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        });

        const { id } = row;
        console.log(id);
        let res = await this.$axios.get(
          `http://127.0.0.1:8080/blogs/photo/delPhoto.php?id=${id}`
        );
        // console.log(res);
        if (res.data[0].status == 200) {
          // 
          this.$message({
            message: "",
            type: "success"
          });
          this.pageNum = 1;
          this.getPhotos();
          // 
          //   this.getPhotos(this.num);
        }
      } catch (error) {
        this.$message({
          type: "info",
          message: ""
        });
      }
    }
  }
};
