<script>
// @ is an alias to /src
import "../../assets/font/iconfont.css";
export default {
  name: "Home",
  data() {
    return {
      input3: "",
      //
      playing: false,
      playIocn: true,
      playPaues: false,
      // logo 
      isroter: false,
    };
  },
  methods: {
    // 
    startPlayOrPause() {
      return this.playing ? this.pause() : this.play();
    },
    // 
    play() {
      this.$refs.audio.play();
      this.playing = true;
      this.playIocn = !this.playIocn;
      this.playPaues = !this.playPaues;
      this.isroter = !this.isroter;
    },
    // 
    pause() {
      this.$refs.audio.pause();
      this.playing = false;
      this.playIocn = !this.playIocn;
      this.playPaues = !this.playPaues;
      this.isroter = !this.isroter;
    },
    // 
    overAudio() {
      // console.log('');
      this.playIocn = !this.playIocn;
      this.playPaues = !this.playPaues;
      this.isroter = !this.isroter;
      this.playing = false;
      this.$refs.audio.load();
    },
    // 
    async userEsc() {
      // 1.
      try {
        await this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        });
        // 
        // 1.
        this.$message({
          type: "success",
          message: "",
        });
        // 2 
        localStorage.removeItem("session");
        // 3login
        this.$router.push("/login");
      } catch (error) {
        this.$message({
          type: "info",
          message: "",
        });
      }
    },
  },
};
</script>

