export default {
  data() {
    return {
      adminUserData: [
        {
          date: "2016-05-02",
          username: "",
          password: " 1518 "
        }
      ]
    };
  },
  created() {
    // 
    this.getAdminUser();
  },
  methods: {
    // 
    async getAdminUser() {
      let res = await this.$axios.get(
        "http://127.0.0.1:8080/blogs/admin/adUser.php"
      );
      // this.data = res.
      if (res.data[0].status == 200) {
        this.adminUserData = res.data[1];
      }
    },
    //
    async delUser(row) {
      // console.log(row);
      try {
        await this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        });
        // console.log("", row);
        const { id } = row;
        let res = await this.$axios.get(
          `http://127.0.0.1:8080/blogs/admin/delUser.php?id=${id}`
        );
        console.log(res);
        if (res.data[0].status == 200) {
          this.$message({
            type: "success",
            message: ""
          });
        }
        this.getAdminUser();
      } catch (error) {
        this.$message({
          type: "info",
          message: ""
        });
      }
    }
  }
};
