export default {
  data() {
    return {
      admTable: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        }
      ],
      total: "",
      pageNum: 1
    };
  },
  created() {
    this.getArticle();
  },
  methods: {
    async getArticle(page = 1) {
      let res = await this.$axios.get(
        `http://localhost:8080/blogs/article/getArticle.php?page=${page}`
      );
      console.log(res);
      if (res.data[0].status == 200) {
        res.data[1].forEach(v => {
          v.date = v.date.substring(0, 10);
          v.contents = v.contents.substring(0, 12) + "....";
        });
        this.admTable = res.data[1];
        this.total = res.data[2][0].total;
      }
    },
    // 
    getArticlePage(num) {
      this.getArticle(num);
    },
    // 
    async delArticle(row) {
      try {
        await this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        });
        const { id } = row;
        // console.log(id);
        // 
        let res = await this.$axios.get(
          `http://localhost:8080/blogs/article/delAritcle.php?id=${id}`
        );
        // console.log(res);
        if (res.data[0].status == 200) {
          // 
          this.$message({
            type: "success",
            message: "",
            duration: 1000
          });
          // 
          this.pageNum = 1;
          this.getArticle();
        }
      } catch (error) {
        this.$message({
          type: "info",
          message: ""
        });
      }
    },
    // 
    alters(row) {
      const { id } = row;
      console.log(id);
      this.$router.push(`/selArticle?id=${id}`);
    }
  }
};
